<template>
    <main-layout>
        <modal 
            v-bind:open="open"
            v-bind:getLast="getLast"
            v-bind:sortedBy="request.sorted_by"
            @update:sortedBy="request.sorted_by = $event"
        />
        <!-- <div class="container">
            <img 
                @click="toggleModal"
                class="bg-white shadow-lg rounded-md cursor-pointer p-2 hover:shadow-2xl "
                src="data:image/png;base64,"/>
        </div> -->
        <div class="grid md:grid-cols-4 md:gap-5 md:mx-10 md:my-16 mx-3 my-2">
            <div v-for="(item, index) in list.data" class="max-w-sm overflow-hidden my-2 shadow-lg bg-white hover:shadow-2xl rounded-lg transform duration-200 cursor-pointer" :key="index">
                <div class="mx-6 border-b grid">
                    <div class="row flex my-4">
                        <div class="flex-1">
                            <div class="grid">
                                <div class=" text-lg font-bold "> 
                                    {{item.account}}
                                </div>
                                <div class=" text-green-600 ">
                                    <!-- {{item.account_name}} -->
                                   {{sortName(item.account_name)}}
                                </div>
                            </div>
                        </div>
                        <div class="flex-1">
                            <div class="grid">
                                <div class=" text-right ">
                                    <strong>{{item.ea_enable ? "Active" : "not active"}} </strong>
                                </div>
                                <div class=" text-xs text-right ">
                                    <!-- Aync: 17 Oct 21 15:09 -->
                                    Sync: {{formatDate(item.broker_time)}}
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="flex my-4">
                        <div class="flex-1">
                            <div class="grid">
                                <div class="">
                                    <strong> Floating </strong>
                                </div>
                                <div class=" text-red-500 font-bold text-2xl ">
                                    {{item.floating}}
                                </div>
                            </div>
                        </div>
                        <div class="flex-1">
                            <div class="grid text-right">
                                <div class=" font-bold  ">
                                    Profit/Day
                                </div>
                                <div
                                    :class="colorProfit(item.profit)" 
                                    class=" font-bold text-2xl ">
                                    {{item.profit}}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class=" flex text-center bg-green-500 text-white py-2 ">
                    <div class="flex-1">
                        <div class="grid">
                            <div class="text-lg font-bold ">
                                {{item.trade}}
                            </div>
                            <div class="text-sm">
                                Open
                            </div>
                        </div>                     
                    </div>
                    <div class="flex-1">
                        <div class="grid">
                            <div class="text-lg font-bold ">
                                {{item.equity}}
                            </div>
                            <div class="text-sm ">
                                Equity USC
                            </div>
                        </div>                      
                    </div>
                    <div class="flex-1">
                        <div class="grid">
                            <div class="text-lg font-bold ">
                                {{item.free_margin}}
                            </div>
                            <div class="text-sm">
                                Free Margin
                            </div>
                        </div>  
                    </div>
                </div>
            </div>
        </div>
        <button type="button" @click="toggleModal" class="bg-white border-4 border-green-300 text-white font-bold py-2 px-2 rounded-lg focus:outline-none focus:shadow-outline btn-orderBy">
            <img 
                width="20"
                src="data:image/png;base64,"/>
        </button>
        <button type="button" @click="refresh" class="bg-white border-4 border-green-300 text-white font-bold py-2 px-2 rounded-lg focus:outline-none focus:shadow-outline btn-refresh">
            <img 
                width="20" v-if="!loading" class=" transition-all duration-300 "
                src="data:image/png;base64,"/>
            <img
                style="display: inline;" v-if="loading" width="20" class=" transition-all duration-300 "
                src="data:image/png;base64,"/>
        </button>
    </main-layout>
</template>

<script>
    import moment from 'moment';
    import axios from 'axios';
    import MainLayout from '@/components/MainLayout'
    import Modal from '@/components/DashboardModal'
    export default {
        data() {
            return {
                loading: false,
                token: localStorage.getItem('token'),
                list: [],
                request:{
                    sorted_by: 'trade_asc',
                },
                open: false,
            }
        },
        components: {
            MainLayout,
            Modal
        },
        mounted() {
            this.getLast();
        },
        methods: {
            async getLast() {
                let that = this;
                this.loading = true;

                await axios.post(process.env.VUE_APP_BASE_URL + "api/dashboard/last-data", 
                        this.request,
                        {
                            headers: {
                                Authorization: `Bearer ` + that.token
                            }
                        })
                    .then(function(data) {
                        console.log(data);
                        if (data.status) {
                            that.loading = false;
                            that.list = data.data;

                            that.open = false;
                        }
                    })
                    .catch(error => {
                        that.loading = false;
                        console.log(error);
                    });
            },
            refresh() {
                this.getLast();
            },
            formatDate(date) {
                return moment(date).format('DD MMM YYYY hh:mm');
            },
            colorProfit(profit) {
                return profit <= 0 ? 'text-red-500' : 'text-green-500';
            },
            sortName(name) {
                return name.substring(0, 18) ;
            },
            toggleModal() {
                this.open = !this.open;
            },
        }
    }
</script>

<style lang="css" scoped>
    .btn-orderBy {
        position: fixed;
        right: 15px;
        bottom: 75px;
    }
    .btn-refresh {
        position: fixed;
        right: 15px;
        bottom: 30px;
    }
</style>