<template>
    <main-layout>
        <div class=" w-full md:grid md:grid-cols-4 md:mx-10 md:my-16 mx-3 my-2 ">
            <div class="col-span-1">
                <form class=" bg-white shadow-md rounded py-8 px-4 md:mr-6 " @submit.prevent="handleSubmit">
                    <p class="mb-5 text-3xl">Form News</p>
                    <div class="mb-4">
                        <label class="block text-gray-700 text-sm font-bold mb-2">
                            Pair
                        </label>
                        <input class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" v-model="form.pair" id="pair" type="text" >
                    </div>
                    <div class="mb-4">
                        <label class="block text-gray-700 text-sm font-bold mb-2">
                            Date News
                        </label>
                        <input 
                            @input="eventDateNews($event.target.value)"
                            class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" 
                            v-model="form.date_news" 
                            id="date_news" 
                            type="datetime-local" >
                    </div>
                    <div class="mb-4">
                        <label class="block text-gray-700 text-sm font-bold mb-2">
                            Date Start
                        </label>
                        <input class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" v-model="form.date_start" id="date_start" type="datetime-local" >
                    </div>
                    <div class="mb-4">
                        <label class="block text-gray-700 text-sm font-bold mb-2">
                            Date Stop
                        </label>
                        <input 
                            class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" 
                            v-model="form.date_stop" 
                            id="date_stop" 
                            type="datetime-local" >
                    </div>
                    <div class="mb-4">
                        <label class="block text-gray-700 text-sm font-bold mb-2">
                            Impact
                        </label>
                        <div class="inline-block relative w-full">
                            <select 
                                v-model="form.impact"
                                class="block appearance-none w-full bg-white border py-2 px-3 rounded shadow leading-tight focus:outline-none focus:shadow-outline">
                                <option value="LOW">LOW</option>
                                <option value="MEDIUM">MEDIUM</option>
                                <option value="HIGH">HIGH</option>
                            </select>
                            <div class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
                                <svg class="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/></svg>
                            </div>
                        </div>
                    </div>
                    <div class="mb-4">
                        <label class="block text-gray-700 text-sm font-bold mb-2">
                            Description
                        </label>
                        <textarea class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" v-model="form.desc" id="desc" type="text" > </textarea>
                    </div>
                    <div class="">
                        <button 
                            type="submit" 
                            class="bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline">
                            Kirim
                        </button>
                        <button 
                            @click="resetForm"
                            type="button" 
                            class="bg-white text-red-500 border-2 font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline ml-3 float-right ">
                            cancel
                        </button>
                        <span >{{loading ? "loading" : ""}}</span>
                    </div>
                </form>
            </div>
            <div class=" hidden sm:hidden md:block lg:block col-span-3 ">
                <div                    
                    class=" bg-white shadow-md rounded py-8 px-4 overflow-x-auto  ">
                    <p class="mb-5 text-3xl">Data News</p>
                    <table 
                        class="shadow-lg bg-white w-full ">
                        <!-- <tr>
                            <th class="text-white border text-center md:px-0 px-5 py-4 " style="background-color: #4680FE">Action</th>
                            <th class="text-white border text-center px-8 py-4" style="background-color: #4680FE">Pair</th>
                            <th class="text-white border text-center px-8 py-4" style="background-color: #4680FE">Date News</th>
                            <th class="text-white border text-center px-8 py-4" style="background-color: #4680FE">Date Start</th>
                            <th class="text-white border text-center px-8 py-4" style="background-color: #4680FE">Date Stop</th>
                            <th class="text-white border text-center px-8 py-4" style="background-color: #4680FE">Impact</th>
                            <th class="text-white border text-center px-8 py-4" style="background-color: #4680FE">Description</th>
                        </tr> -->
                        <tr v-for="(item, index) in table"  :key="index">
                            <td class="border px-5 py-4 text-xs">
                                <img @click="edit(item.pid_news)" width="20" style="display: inline" class=" cursor-pointer " src="data:image/png;base64,"/>
                                <img @click="remove(item.pid_news)" width="20" style="display: inline" class="float-right cursor-pointer "  src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABgAAAAYCAYAAADgdz34AAAABmJLR0QA/wD/AP+gvaeTAAAAZElEQVRIiWNgGOqAkRhFP428Ghj/M9Qji/1n+N/Jfn57BckW/DL0+k+8+zAB2/ltKGYyUWLYyABYI5nceEAPfwYGOsQBQQvYzm9jxOYyXOIkW0ApGLVg1IJRC+hgwdAv7IY+AABHeRpR7gJWRgAAAABJRU5ErkJggg=="/>
                            </td>
                            <td class="border px-5 py-4 text-xs">{{item.pair}}</td>
                            <td class="border px-5 py-4 text-xs">{{item.date_news}}</td>
                            <td class="border px-5 py-4 text-xs">{{item.date_start}}</td>
                            <td class="border px-5 py-4 text-xs">{{item.date_stop}}</td>
                            <td class="border px-5 py-4 text-xs">{{item.impact}}</td>
                            <td class="border px-5 py-4 text-xs">{{item.desc}}</td>
                        </tr>
                    </table>
                </div>
            </div>
            <!-- <div class=" sm:mt-9 mt-9 block sm:block md:hidden lg:hidden col-span-3">
                <div class="bg-white shadow-md rounded py-8 overflow-x-auto" >
                    <p class="mb-5 text-3xl ml-2">Data News</p>
                    <table 
                        class=" shadow-lg bg-white w-full ">
                        <tr v-for="(item, index) in table" :key="index">
                            <td class="border px-5 py-4 text-xs">
                                <img @click="edit(item.pid_news)" width="20" style="display: inline" class=" cursor-pointer " src="data:image/png;base64,"/>
                                <img @click="remove(item.pid_news)" width="20" style="display: inline" class=" ml-2 cursor-pointer "  src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABgAAAAYCAYAAADgdz34AAAABmJLR0QA/wD/AP+gvaeTAAAAZElEQVRIiWNgGOqAkRhFP428Ghj/M9Qji/1n+N/Jfn57BckW/DL0+k+8+zAB2/ltKGYyUWLYyABYI5nceEAPfwYGOsQBQQvYzm9jxOYyXOIkW0ApGLVg1IJRC+hgwdAv7IY+AABHeRpR7gJWRgAAAABJRU5ErkJggg=="/>
                                <div>
                                    bawah
                                </div>
                            </td>
                        </tr>
                    </table>
                </div>
            </div> -->
        </div>
    </main-layout>
</template>

<script>
    import axios from 'axios';
    import moment from 'moment';
    // import {baseUrl} from '@/helpers';
    import MainLayout from '@/components/MainLayout';

    const initialState = () => {
        return {
            pid_news: null,
            pair: null,
            date_news: null,
            date_start: null,
            date_stop: null,
            impact: null,
            desc: null,
        };
    }

    export default {
        data() {
            return {
                token: localStorage.getItem('token'),
                loading: false,
                form: initialState(),
                table: [],
            };
        },
        components: {
            MainLayout
        },
        mounted() {
            this.getData();
        },
        computed: {
            hideTable() {
                return window.innerWidth <= 760 ? 'hidden' : '';
            }
        },
        methods: {
            async getData() {
                let that = this;

                await axios.post(process.env.VUE_APP_BASE_URL + "api/news/getData", {}, {
                        headers: {
                            Authorization: `Bearer ` + that.token
                        }
                    })
                    .then(function({
                        data
                    }) {
                        if (data.status) {
                            that.table = data.data;
                        }
                    })
                    .catch(error => {
                        console.log(error);
                    });
            },
            async handleSubmit() {
                let that = this;
                this.loading = true;

                if (this.form.pair == null || this.form.impact == null) {
                    console.log('form kosong');

                    this.loading = false;
                    this.$swal.mixin({
                            toast: true,
                            position: "top-end",
                            showConfirmButton: false,
                            timerProgressBar: true,
                            timer: 2000,
                        })
                        .fire({
                            icon: "warning",
                            title: "Maaf, form todak boleh kosong",
                        });
                    return false;
                }

                await axios.post(process.env.VUE_APP_BASE_URL + "api/news/store",
                        this.form, {
                            headers: {
                                Authorization: `Bearer ${that.token}`
                            }
                        })
                    .then(function({
                        data
                    }) {
                        if (data.status != undefined && data.status) {
                            that.$swal.mixin({
                                    toast: true,
                                    position: "top-end",
                                    showConfirmButton: false,
                                    timerProgressBar: true,
                                    timer: 2000,
                                })
                                .fire({
                                    icon: "success",
                                    title: data.remark
                                });
                        } else if (data.status != undefined && !data.status) {
                            that.$swal.mixin({
                                    toast: true,
                                    position: "top-end",
                                    showConfirmButton: false,
                                    timerProgressBar: true,
                                    timer: 2000,
                                })
                                .fire({
                                    icon: "warning",
                                    title: data.remark
                                });

                            console.log(data.remark);
                        }
                    })
                    .catch(error => {
                        console.log(error);
                    })
                    .finally(() => {
                        this.form = initialState();
                        this.loading = false;
                        this.getData();
                    });
            },
            async remove(pid_news) {
                let that = this;
                let foundData = this.table.filter(item => item.pid_news == pid_news)[0];

                return await this.$swal.fire({
                    icon: 'question',
                    title: 'Do you want to delete this data ?',
                    showDenyButton: true,
                    confirmButtonText: 'Yes',
                    denyButtonText: `No`,
                }).then((result) => {
                    /* Read more about isConfirmed, isDenied below */
                    if (result.isConfirmed) {
                        axios.post(process.env.VUE_APP_BASE_URL + "api/news/delete",
                                foundData, {
                                    headers: {
                                        Authorization: `Bearer ${that.token}`
                                    }
                                })
                            .then(function({
                                data
                            }) {
                                if (data.status != undefined && data.status) {
                                    that.$swal.mixin({
                                            toast: true,
                                            position: "top-end",
                                            showConfirmButton: false,
                                            timerProgressBar: true,
                                            timer: 2000,
                                        })
                                        .fire({
                                            icon: "success",
                                            title: data.remark
                                        });

                                    that.getData();
                                } else if (data.status != undefined && !data.status) {
                                    that.$swal.mixin({
                                            toast: true,
                                            position: "top-end",
                                            showConfirmButton: false,
                                            timerProgressBar: true,
                                            timer: 2000,
                                        })
                                        .fire({
                                            icon: "warning",
                                            title: data.remark
                                        });

                                    console.log(data.remark);
                                }
                            })
                            .catch(error => {
                                console.log(error);
                            })
                    } else if (result.isDenied) {
                        this.$swal.fire('Changes are not saved', '', 'info')
                    }
                })
            },
            edit(pid_news) {
                // let that = this;        
                let foundData = this.table.filter(item => item.pid_news == pid_news)[0];

                this.form.pid_news = foundData.pid_news;
                this.form.pair = foundData.pair;
                this.form.impact = foundData.impact;
                this.form.desc = foundData.desc;
                this.form.date_news = moment(foundData.date_news).format('YYYY-MM-DDThh:mm:ss');
                this.form.date_start = moment(foundData.date_start).format('YYYY-MM-DDThh:mm:ss');
                this.form.date_stop = moment(foundData.date_stop).format('YYYY-MM-DDThh:mm:ss');
            },
            eventDateNews() {
                if (this.form.pid_news == null) {
                    this.form.date_start = moment(this.form.date_news).subtract({
                        hours: 5
                    }).format('YYYY-MM-DDThh:mm:ss');
                    this.form.date_stop = moment(this.form.date_news).add({
                        hours: 2
                    }).format('YYYY-MM-DDThh:mm:ss');
                }
            },
            resetForm() {
                this.form = initialState();
            },
        }

    }
</script>

<style lang="css" scoped>

</style>

// ketika set date_news // maka date_start = date_news - 5 // date_stop = date_news + 2 // impact => low, medium, high // description type = "textarea"